import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';

class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Tooltip </h1>
                <blockquote className='page-tip'>
                    <br/>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                    <Label className='code-box'>
                        <Demo3></Demo3>
                    </Label>
                </Label>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                    <Label className='code-box'>
                        <Demo4></Demo4>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Tooltip</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'title', desc: '', type: 'string', default: ''},
                    {param: 'theme', desc: 'defaultblackprimaryerror', type: 'string', default: 'black'},
                    {param: 'delay', desc: '', type: 'number', default: '0'},
                    {param: 'trigger', desc: ' click or hover', type: 'string', default: 'hover'},
                    {param: 'onVisibleChange', desc: '', type: 'function', default: ''},
                    {param: 'align', desc: '12', type: 'string', default: 'top'}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>setTitle(title)</i>
                        <span></span>
                        <ul>
                            <li>
                                title {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>show()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>hide()</i>
                        <span></span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



